import "../formHuesped/FormHuesped.css"
import "./EditarUsuario.css"
import { Button, Modal } from 'react-bootstrap';
import React, { useState } from 'react';
import { Formik, Form, Field, ErrorMessage } from 'formik';

function ModalContrasea() {
    //modal
    const handleClose = () => setTimeout(() => handleClose(window.location.replace('/editarperfil'), 0));
    //validaciones
    const [formularioEnviado, cambiarFormularioEnviado] = useState(false)

    return (

        <Formik
            initialValues={{
                actual: '',
                newC: '',
                newAgainC: ''
            }}
            validate={(valores) => {
                let errores = {};
                // Validacion contrasea "actual"
                if (!valores.actual) {
                    errores.actual = 'Por favor ingresa su contrasea actual'
                } else if (!/^[A-Za-z\d$@$!%*?&]{8,15}$/.test(valores.actual)) {
                    errores.actual = 'Contrasea incorrecta'
                }
                // Validacion newC
                if (!valores.newC) {
                    errores.newC = 'Por favor ingresa un nueva contrasea'
                } else if (!/^[A-Za-z\d$@$!%*?&]{8,15}$/.test(valores.newC)) {
                    errores.newC = 'Digite una contrasea valida'
                }
                // Validacion newAgainC
                if (!valores.newAgainC) {
                    errores.newAgainC = 'La contrasea no coincide'
                }
                else if (valores.newAgainC !== valores.newC) {
                    errores.newAgainC = "La contrasea no coincide"
                }
                return errores;
            }}
            onSubmit={(valores, { resetForm }) => {
                resetForm();
                console.log('Formulario enviado');
                cambiarFormularioEnviado(true);
                setTimeout(() => cambiarFormularioEnviado(false), 5000);
                setTimeout(() => handleClose(false), 2000)
                setTimeout(() => window.location.replace('/login'), 1000);
            }}
        >
            {({ errors }) => (
                <Form>
                    <Modal.Body>
                        <div>
                            <label>Digite la Contrasea Actual</label>
                            <Field
                                className="formhuesped-input"
                                type="password"
                                name="actual"
                                id="actual"
                            />
                        </div>
                        <ErrorMessage name="actual" component={() => (<div className="error">{errors.actual}</div>)} />
                        <div>
                            <label>Contrasea Nueva </label>
                            <Field
                                className="formhuesped-input"
                                type="password"
                                name="newC"
                                id="newC"
                            />
                        </div>
                        <ErrorMessage name="newC" component={() => (<div className="error">{errors.newC}</div>)} />
                        <div>
                            <label>Confirmar Contrasea</label>
                            <Field
                                className="formhuesped-input"
                                type="password"
                                name="newAgainC"
                                id="newAgainC"
                            />
                        </div>
                        <ErrorMessage name="newAgainC" component={() => (<div className="error">{errors.newAgainC}</div>)} />
                        {formularioEnviado && <p className="exito">Contrasea cambiada con exito!</p>}
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={handleClose}>Cancelar</Button>
                        <Button className=" editar-usuario-primary" type="submit">Cambiar Contrasea</Button>
                    </Modal.Footer>
                </Form>
            )}
        </Formik>
    );
}
export default ModalContrasea